const URL = 'https://api.thecatapi.com/v1';
const API_KEY =
  'live_po3LnyQG53kvSpm8751TReioKDhiLc437K6zegXLzGDDYSx81GWGF35D0zLFkTm8';

async function fetchBreeds() {
  try {
    const response = await fetch(`${URL}/breeds?api_key=${API_KEY}`);
    if (!response.ok) {
      throw new Error(`Error: ${response.status}`);
    }
    return await response.json();
  } catch (error) {
    console.error('Error fetching breeds:', error.message);
    throw error;
  }
}

async function fetchCatByBreed(breedId) {
  try {
    const response = await fetch(`${URL}/images/search?api_key=${API_KEY}&breed_ids=${breedId}`);
    if (!response.ok) {
      throw new Error(`Error: ${response.status}`);
    }
    return await response.json();
  } catch (error) {
    console.error(`Error fetching cat by breed ${breedId}:`, error.message);
    throw error;
  }
}

export { fetchBreeds, fetchCatByBreed };